.App {
  color: white;
  max-width: 400px;
  margin: 0 auto;
}

.CardApp {
  color: white;
  margin: 0 auto;
  padding: min(5vh, 5vw);
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  height: 100vh;
  width: min(calc(2.5 / 3.5 * 100vh), 100vw);
}

a {
  text-decoration: none;
}

a,
a:visited {
  color: #edbb30;
  font-weight: 400;
}

.performances,
.aboutme,
.App .logoWrapper,
.App .contactWrapper {
  background-color: #7f00ff;
}

.App .logoWrapper {
  text-align: center;
}

.App .centerLogo {
  width: 50%;
  margin: -40px 0;
}

.App .headerWrapper {
  padding: 12px;
}

.luis {
  font-size: 60px;
  font-weight: 800;
  line-height: 40px;
}

.shrestha {
  font-size: 40px;
}

.tagline {
  margin-top: 8px;
}

.firstPic {
  max-height: 100%;
  max-width: 100%;
  margin-bottom: -4px;
}

.performances,
.aboutme {
  font-size: 32px;
  font-weight: bold;
  padding: 24px 12px;
}

h3 {
  text-align: center;
}

.secondPic {
  width: 80%;
  margin: 0 10%;
}

.App .content,
.App .aboutContent {
  padding: 4px 12px;
}

.CardApp .content,
.CardApp .aboutContent {
  padding: 4px 12px;
}

.stageMagicWrapper,
.App .aboutContent {
  background-color: #161419;
}

.thirdPic {
  width: 50%;
  margin: 0 25%;
}

.App .profilePic {
  max-width: 100%;
  max-height: 100%;
}

.CardApp .profilePic {
  max-width: 100%;
  max-height: 100%;
}

.App .aboutContent {
  margin-bottom: -20px;
  margin-top: -4px;
}

.App .contactWrapper {
  text-align: left;
  padding: 0 12px 12px 12px;
}

.CardApp .contactWrapper {
  text-align: left;
  padding: 0 12px 12px 12px;
}

@media (prefers-reduced-motion: no-preference) {
  .tagline {
    animation: reveal 1 2s ease-in;
  }
  img,
  p,
  h3 {
    animation: reveal 1 6s ease-out;
  }
}

@keyframes reveal {
  from {
    filter: opacity(0);
  }
  to {
    filter: opacity(1);
  }
}
